<template>
  <div>
    <el-button @click.native="dialogTableVisible=true" size="mini" type="primary">选择api列表</el-button>
    <div class="tag-warpper">
      <div v-for="(tag,index) in checkApiItems" :key="index" class="tag-warpper__items">
      <el-tag  closable @close='getTagItem(tag,index)' :disable-transitions='true'>
        {{tag.apiName}} &nbsp;&nbsp;{{tag.apiUrl}}
      </el-tag>
      </div>
    </div>
    <el-dialog title="选择API列表" :visible="dialogTableVisible" width="90%" :lock-scroll='true' :append-to-body='true' :before-close='close'>
      <div class="flex-card-box">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>已设置的api列表</span>
          </div>
          <el-checkbox-group v-model="checkApiIds">
            <ul class="check-items-box">
              <li v-for="(item,index) in checkApiItems" :key='index' class="check-items">
                <el-checkbox class="check-items-label" :label="item.id">
                  ({{item.apiName}}) {{item.apiUrl}}
                </el-checkbox>
              </li>
            </ul>
          </el-checkbox-group>
          <div class="card-footer">
            <el-checkbox v-model="allApiIds" @change="setAllApiIds">全选</el-checkbox>
            <el-button size='mini' type="primary" @click="removeCheckItems" :disabled="checkApiIds.length<=0">移除</el-button>
          </div>
        </el-card>
        <div class=" box-right">
          <span class="el-icon-d-arrow-right"></span>
        </div>
        <div class="box-table">
          <section class="cms-list-query">
            <div class="query-group">
              <label>api地址</label>
              <el-input v-model="queryParams.apiUrl" @change="query" style="width:400px;"></el-input>
            </div>
            <div class="query-group">
              <label>api名称</label>
              <el-input v-model="queryParams.apiName" @change="query"></el-input>
            </div>
            <div class="query-group">
              <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
            </div>
          </section>
          <el-table :data="tableData" style="width:100%;overflow-y:hidden; cursor: pointer;" stripe v-loading="loading" @select='getCheckItems' @row-click='checkRows' ref='dataTable' @select-all='getCheckItemsAll' max-height='500'>
            <el-table-column type="selection" width="46" fixed='left'></el-table-column>
            <el-table-column prop="apiName" label="api名称" min-width="120px">
               <template slot-scope="scope">
                    <div class="text-overflow" :title="scope.row.apiName">
                      {{scope.row.apiName}}
                    </div>
                 </template>
            </el-table-column>
            <el-table-column prop="apiUrl" label="api地址" min-width="220px"></el-table-column>
            <el-table-column prop="useScene" label="使用场景">
               <template slot-scope="scope">
                    <div class="text-overflow" :title="scope.row.useScene">
                      {{scope.row.useScene}}
                    </div>
                 </template>

            </el-table-column>
          </el-table>
          <cms-pagination @change="getPage" :total='total'></cms-pagination>
        </div>
      </div>
      <div slot="footer" style="text-align:center">
        <el-button type="primary" @click="close" >确定</el-button>
        <el-button  @click="close" >取消</el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
import list from '@/mixins/list'
import { apiManagerList } from '@/api/admin/apiManger.js'
var defaultData = {
  apiName: '',
  apiUrl: '',
  size: 10,
  page: 1,
  platformType: 1
}
export default {
  name: 'apiManger',
  mixins: [list],
  props: {
    type: {
      type: [String, Number],
      default: 1
    },
    apis: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
      dialogTableVisible: false, // 弹窗显示
      queryParams: Object.assign({}, defaultData),
      typeList: [],
      allApiIds: false, // 选中的id的全选
      checkApiIds: [], // 选中的id数组，控制移除用
      checkApiItems: [], // 勾选的权限对象集合
      selfAllItems: [], // 处理多选用的临时数组
      allCheck: false // 全选状态，默认全不选
    }
  },
  computed: {},
  methods: {
    defaultToggleSelection() {
      // 处理回填的默认勾选
      this.$refs.dataTable.clearSelection()
      this.$nextTick(() => {
        this.tableData.map((v, index) => {
          for (let i = 0; i < this.checkApiItems.length; i++) {
            if (v.id === this.checkApiItems[i].id) {
              this.$refs.dataTable.toggleRowSelection(
                this.tableData[index],
                true
              )
              break
            }
          }
        })
      })
    },
    getTableData(callback, params, isPage = true) {
      // 获取表格数据,处理翻页选中
      this.tableData = []
      this.loading = true
      this.callback = callback
      this.isPage = isPage
      setTimeout(() => {
        callback(params)
          .then(res => {
            if (res.code === 200) {
              if (isPage) {
                this.total = res.data.totalElements
                this.tableData = res.data.content
                this.defaultToggleSelection()
              } else {
                this.tableData = res.data
              }
            } else {
              this.$errorMessage(res.message)
            }
            this.loading = false
          })
          .catch(res => {
            this.loading = false
            // this.$errorMessage('加载失败，请重试！')
          })
      }, 1)
    },
    getTagItem(item, index) {
      // 标签移除
      this.$refs.dataTable.toggleRowSelection(item) // 取消表格选中
      this.checkApiItems.splice(index, 1)
    },
    setAllApiIds(value) {
      // 设置按钮全选状态
      if (value) {
        this.checkApiIds = this.checkApiItems.map(v => v.id)
      } else {
        this.checkApiIds = []
      }
    },
    removeCheckItems() {
      // 移除选中
      const arr = []
      this.checkApiItems.map(v => {
        if (this.checkApiIds.indexOf(v.id) === -1) {
          arr.push(v)
        } else {
          this.$refs.dataTable.toggleRowSelection(v)
        }
      })
      this.checkApiItems = arr
      this.checkApiIds = []
    },
    toggleApiItems(row, buttonType, allCheckStuatus) {
      // 循环判断一下有没有已经选中过的
      let state = false
      let $index = -1
      this.checkApiItems.map((v, index) => {
        if (v.id === row.id) {
          $index = index
          state = true
          return
        }
      })
      // 判断当前点击的复选框类型  allClick-全选/反选
      if (buttonType === 'allClick') {
        if (allCheckStuatus && !state) {
          // 全选且不存在，进行添加
          this.checkApiItems.push(row)
        } else if (!allCheckStuatus && state) {
          // 反选且存在，进行删除
          this.checkApiItems.splice($index, 1)
        }
      } else {
        if (state) {
          // 进行删除
          this.checkApiItems.splice($index, 1)
        } else {
          // 进行添加
          this.checkApiItems.push(row)
        }
      }
    },
    checkRows(row) {
      // 单击行选中
      this.checkApiIds = []
      this.$refs.dataTable.toggleRowSelection(row) // 取消表格选中
      this.toggleApiItems(row)
    },
    getCheckItemsAll(selection) {
      // 获取到当前选中的所有对象
      if (selection.length > 0) {
        this.allCheck = true
        this.selfAllItems = this.$cms.deepCopy(selection)
      } else {
        this.allCheck = false
      }
      this.selfAllItems.map(v => {
        this.toggleApiItems(v, 'allClick', this.allCheck)
      })
    },
    getCheckItems(selection, row) {
      // 保留住选中的数据行
      this.toggleApiItems(row)
    },
    close(done) {
      this.dialogTableVisible = false
    }
  },

  created() {
    this.queryParams.platformType = this.type
    this.getTableData(apiManagerList, this.queryParams)
  },
  watch: {
    apis: {
      handler() {
        this.checkApiItems = this.$cms.deepCopy(this.apis)
        // this.defaultToggleSelection()
      },
      deep: true,
      immediate: true
    },
    checkApiIds: {
      handler() {
        if (
          this.checkApiIds.length === this.checkApiItems.length &&
          this.checkApiIds.length > 0
        ) {
          this.allApiIds = true
        } else {
          this.allApiIds = false
        }
      },
      deep: true
    },
    checkApiItems: {
      handler() {
        this.$emit('get', this.checkApiItems)
      },
      deep: true
    }
  }
}
</script>

<style lang="scss" >
.flex-card-box {
  padding: 12px;
  height: 666px;
  box-sizing: border-box;
  overflow: hidden;
}
.box-card {
  float: left;
  width: 340px;
  .el-card__body {
    padding: 12px 0 0px 12px;
  }
}
.box-right {
  width: 50px;
  float: left;
  height: 100%;
  line-height: 642px;
  text-align: center;
}
.box-table {
  margin-left: 50px+340px;
  padding: 12px;
  // border: 1px solid #e0e0e0;
  border-radius: 4px;
  height: 632px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.check-items-box {
  height: 510px;
  overflow-x: hidden;
  overflow-y: auto;
}
.check-items-label {
  .el-checkbox__label {
    padding-right: 5px;
    display: inline-block;
    width: 322px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: relative;
    top: 4px;
  }
}
.check-items {
  height: 32px;
  line-height: 32px;
  border-bottom: 1px dashed #e0e0e0;
  width: 100%;
}
.card-footer {
  width: 100%;
  padding: 12px 12px 12px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tag-warpper {
  width: 100%;
  padding-top: 12px ;
  &__items{
    margin-bottom: 5px
  }
}
</style>




