<template>
  <div v-loading="loading">
    <cms-breadcrumb
      to="/menuManager"
      type="list"
      :params="{platformType:$route.query.platformType}"
      @click="rest"
    >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse="false">
        <li>可进行系统菜单的权限新增</li>
      </cms-prompt>
      <h3 class="cms-segmentation">基础信息</h3>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="上级菜单" prop="parentIds" :error="errorMessage.parentIds">
          <el-cascader
            :options="options"
            :props="props"
            :change-on-select="true"
            v-model="formData.parentIds"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="菜单名称" prop="menuName" :error="errorMessage.menuName">
          <el-input v-model="formData.menuName" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item
          label="路由地址(path)"
          prop="routingIdentifier"
          :error="errorMessage.routingIdentifier"
        >
          <el-input
            v-model="formData.routingIdentifier"
            @change="autoAccessIdentifier"
            maxlength="100"
          ></el-input>
        </el-form-item>
        <el-form-item label="页面路径(component)" prop="pagePath" :error="errorMessage.pagePath">
          <el-input v-model="formData.pagePath" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="权限标识" prop="accessIdentifier" :error="errorMessage.accessIdentifier">
          <el-input v-model="formData.accessIdentifier" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="菜单图标" prop="menuImg" :error="errorMessage.menuImg">
          <el-input v-model="formData.menuImg" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item
          label="路由重定向标识"
          prop="redirectedRouting"
          :error="errorMessage.redirectedRouting"
        >
          <el-input v-model="formData.redirectedRouting" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error="errorMessage.sortNum">
          <el-input v-model.number="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="菜单类型" prop="menu_type">
          <el-radio-group v-model="formData.menu_type" @change="setShow">
            <el-radio :label="1">菜单</el-radio>
            <el-radio :label="2">权限</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="可见" prop="show" :error="errorMessage.show">
          <cms-switch v-model="formData.show"></cms-switch>
        </el-form-item>
        <el-form-item label="是否展示下级菜单布局" prop="lowerMenu" :error="errorMessage.lowerMenu">
          <cms-switch v-model="formData.lowerMenu"></cms-switch>
        </el-form-item>
        <el-form-item label="api接口信息">
          <api-manger @get="getApiMenuId" v-if="status" :type="$route.query.platformType"></api-manger>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type="rest" @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import apiManger from './apiManger'
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import {
  menuManagerFindAll,
  menuManagerSaveChild,
  checkRouting
} from '@/api/admin/menuManager.js'

var defaultData = {
  parentIds: [], // 根目录值
  parentId: '',
  menuName: '',
  sortNum: 10,
  platformType: '',
  lowerMenu: false,
  menu_type: 1,
  show: true,
  routingIdentifier: '',
  pagePath: '',
  accessIdentifier: '',
  metaValue: '',
  menuImg: '',
  redirectedRouting: '',
  apiIds: []
}
export default {
  components: {
    apiManger
  },
  mixins: [form],
  data() {
    var ischeckRouting = (rule, value, callback) => {
      // 验证路由唯一
      checkRouting({
        platformType: this.$route.query.platformType,
        routing: value
      }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('路由已存在，不允许重复添加'))
          }
        }
      })
    }
    return {
      options: [],
      props: {
        value: 'id',
        label: 'name',
        children: 'childs',
        disabled: 'isAuth'
      },
      formData: Object.assign({}, defaultData),
      rules: {
        parentIds: [rules.required('必须选择一个上级菜单')],
        menuName: [
          rules.required('菜单名称必须填写'),
          rules.max(15, '最大长度为15个字符')
        ],
        routingIdentifier: [
          rules.required('路由地址必须填写'),
          rules.custom(ischeckRouting)
        ],
        sortNum: [rules.required('排序值必填'), rules.number()],
        menuid: [rules.required()]
      }
    }
  },
  methods: {
    setShow(value) {
      if (value === 2) {
        this.formData.show = false
      }
    },
    autoAccessIdentifier(value) {
      // 自动填写权限
      this.formData.accessIdentifier = value
    },
    getMenuTree() {
      menuManagerFindAll({ platformType: this.$route.query.platformType }).then(
        res => {
          const root = [{ id: 0, name: '根目录' }]

          this.options = root.concat(res.data)
        }
      )
    },
    getApiMenuId(items) {
      // 返回的是选中的菜单api对象
      this.formData.apiIds = items.map(v => v.id)
    },
    save(state) {
      var params = {
        // 构造传递字符串
        coreMenu: this.$cms.deepCopy(this.formData),
        apiIds: this.formData.apiIds
      }
      params.coreMenu.parentId =
        params.coreMenu.parentIds[params.coreMenu.parentIds.length - 1] // 最后一层是他的父级id
      delete params.coreMenu.apiIds
      this.saveForm(
        menuManagerSaveChild,
        params,
        state,
        '/menuManager',
        'list',
        { platformType: this.formData.platformType }
      ).then(res => {
        // 刷新下栏目树
        this.getMenuTree()
        this.formData.apiIds = []
      })
    }
  },
  created() {
    // 初始化赋值
    this.formData.parentIds = this.$cms.arrayToNumber(
      this.$route.query.nodeIds
    )
    this.formData.platformType = this.$route.query.platformType
    this.getMenuTree()
  }
}
</script>

<style>
</style>



